ul {
  list-style: none;
}

input, button {
  border: none;
}

ul, h1, h2, h3, p, button, input, img {
  margin: 0;
  padding: 0;
}

a {
  color: #000;
  text-decoration: none;
}

body {
  font-family: Roboto;
}

.container {
  box-sizing: border-box;
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 20px;
}

@media (width <= 768px) {
  .container {
    width: 95%;
    padding: 20px 15px;
  }
}

@media (width <= 480px) {
  .container {
    width: 100%;
    padding: 20px 10px;
  }
}

.lesson-modal, .course-modal {
  z-index: 1000;
  background-color: #00000080;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.lesson-modal__content, .course-modal__content {
  text-align: center;
  background-color: #fff;
  border: 1px solid #888;
  border-radius: 8px;
  width: 50%;
  max-width: 600px;
  margin: 10% auto;
  padding: 20px;
}

.lesson-modal__close, .course-modal__close {
  color: #aaa;
  float: right;
  cursor: pointer;
  font-size: 28px;
  font-weight: bold;
}

.lesson-modal__close:hover, .course-modal__close:hover {
  color: #000;
}

.lesson-modal__title, .course-modal__title {
  margin-bottom: 20px;
}

.lesson-modal__label, .course-modal__label {
  margin: 10px 0 5px;
  display: block;
}

.lesson-modal__input, .course-modal__input, .lesson-modal__select, .course-modal__select {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 80%;
  margin-bottom: 15px;
  padding: 10px;
}

.lesson-modal__submit, .course-modal__submit {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  width: 80%;
  margin: 0 auto;
  padding: 10px;
  display: block;
}

.lesson-modal__submit:hover, .course-modal__submit:hover {
  background-color: #0056b3;
}

@media (width <= 1024px) {
  .lesson-modal__content, .course-modal__content {
    width: 70%;
  }

  .lesson-modal__input, .course-modal__input, .lesson-modal__select, .course-modal__select, .lesson-modal__submit, .course-modal__submit {
    width: 90%;
  }
}

@media (width <= 768px) {
  .lesson-modal__content, .course-modal__content {
    width: 90%;
  }

  .lesson-modal__input, .course-modal__input, .lesson-modal__select, .course-modal__select, .lesson-modal__submit, .course-modal__submit {
    width: 100%;
  }
}

.header {
  background-color: #fff;
  padding: 1rem 2rem;
  position: relative;
  box-shadow: 0 4px 8px #0000001a;
}

.header nav {
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.header-list {
  gap: 1.5rem;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header-list__item {
  margin: 0;
}

.header-list__link {
  color: #000;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
}

.header-list__link:hover, .header-list__link:focus, .header-list__link:active {
  color: #0089f7;
}

@media (width <= 768px) {
  .header nav {
    justify-content: space-between;
  }

  .header-list {
    background-color: #fff;
    flex-direction: column;
    align-items: center;
    padding: 1rem 0;
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    box-shadow: 0 4px 8px #0000001a;
  }

  .header-list.active {
    display: flex;
  }

  .header-list__item {
    margin: .5rem 0;
  }

  .header-menu-icon {
    cursor: pointer;
    flex-direction: column;
    justify-content: space-around;
    width: 30px;
    height: 25px;
    display: flex;
  }

  .header-menu-bar {
    background-color: #000;
    width: 100%;
    height: 3px;
    transition: all .3s;
  }

  .header-menu-icon.active .header-menu-bar:first-child {
    transform: translateY(11px)rotate(45deg);
  }

  .header-menu-icon.active .header-menu-bar:nth-child(2) {
    opacity: 0;
  }

  .header-menu-icon.active .header-menu-bar:nth-child(3) {
    transform: translateY(-11px)rotate(-45deg);
  }
}

@media (width >= 481px) and (width <= 1024px) {
  .header-list {
    gap: 1rem;
  }

  .header-list__link {
    font-size: 16px;
  }
}

