}

.word-scramble__content {
  display: block;
}

.word-scramble__word {
  -webkit-user-select: none;
  user-select: none;
  text-align: center;
  letter-spacing: 24px;
  word-break: break-all;
  text-transform: uppercase;
  margin-right: -24px;
  font-size: 33px;
  font-weight: 500;
}

.word-scramble__details {
  margin: 25px 0 20px;
}

.word-scramble__hint {
  margin-bottom: 10px;
  font-size: 18px;
}

