  font-weight: 500;
}

.word-scramble__input {
  border: 1px solid #bfbfbf;
  border-radius: 5px;
  outline: none;
  width: 96.5%;
  height: 60px;
  padding: 0 16px;
  font-size: 18px;
}

.word-scramble__input:focus {
