.game {
  text-align: center;
}

.game__title {
  color: #333;
  margin-bottom: 40px;
  font-size: 36px;
  font-weight: bold;
}

.game__board {
  grid-gap: 10px;
  grid-template-columns: repeat(4, 150px);
  justify-content: center;
  margin-bottom: 20px;
  display: grid;
}

.game__card {
  color: #333;
  cursor: pointer;
  text-align: center;
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 150px;
  height: 150px;
  padding: 10px;
  font-size: 18px;
  transition: transform .3s;
  display: flex;
  box-shadow: 0 2px 5px #0003;
}

.game__card:hover {
  transform: scale(1.05);
}

.game__card--matched {
  color: #fff;
  cursor: default;
  background-color: #4caf50;
}

.game__card--flipped {
  color: #fff;
  background-color: #2196f3;
}

.game__reset-button {
  color: #fff;
  cursor: pointer;
  background-color: #000;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

.game__reset-button:hover {
  background-color: #0089f7;
}

@media (width <= 480px) {
  .game__title {
    margin-bottom: 30px;
    font-size: 28px;
  }

  .game__board {
    grid-gap: 8px;
    grid-template-columns: repeat(2, 120px);
  }

  .game__card {
    width: 120px;
    height: 120px;
    font-size: 16px;
  }

  .game__reset-button {
    padding: 8px 16px;
    font-size: 14px;
  }
}

@media (width >= 481px) and (width <= 768px) {
  .game__title {
    margin-bottom: 35px;
    font-size: 32px;
  }

  .game__board {
    grid-gap: 10px;
